<template>
	<view class="shop-list">
		<view class="shop-title f-color">
			<view  class="shop-item" v-for="(item,index) in shopList.data" :key="index" @click="changTab(index)">
				<view :class="index === shopList.currentIndex ? 'f-active-color' : '' " >{{item.name}}</view>
				<view class="shop-icon">
					<view class="iconfont icon-xiangshangjiantou up" :class="item.status === 1 ? 'f-active-color' : '' "></view>
					<view class="iconfont icon-xiajiantou down" :class="item.status === 2 ? 'f-active-color' : '' "></view>
				</view>
			</view>
			
		</view>
			
		<Lines />
		<CommodityList v-if="dataList.length > 0" :dataList="dataList" />
		<view class="" v-else>暂无商品</view>
	</view>
</template>

<script>
	import $http from "../../common/api/request.js"
	import Lines from "./Lines.vue"
	import CommodityList from "./CommodityList.vue"
	export default {
		props:{
			keyword:String
		},
		data() {
			return {
				shopList:{
					currentIndex:0,
					data:[
						{name:"价格",status:1,key:"pprice"},
						{name:"折扣",status:0,key:"discount"},
						{name:"品牌",status:0}
					]
				},
				dataList: []
			};
		},
		computed:{
			orderBy(){
				
				let obj = this.shopList.data[this.shopList.currentIndex]
				let val = obj.status === "1" ? "asc" :"desc"
				return {
					[obj.key]:val
				}
				
			}
		},
		mounted() {
			this.getData()
		},
		components: {
			Lines,
			CommodityList
		},
		methods:{
			//请求搜索数据的方法
			getData(){
				$http.request({
					url:"/goods/search",
					data:{
						name:this.keyword,
						...this.orderBy
					}
				}).then((res)=>{
					this.dataList = res
				})
			},
			changTab(index){
				//点击排序 重新渲染页面
				this.getData()
				console.log(111);
				//索引值 上下箭头的逻辑
				let idx = this.shopList.currentIndex
				//具体哪一个对象
				let item = this.shopList.data[idx]
				if(idx == index){
					return item.status = item.status === 1 ? 2 : 1
				}
				//新的值
				let newItem = this.shopList.data[index]
				item.status = 0
				newItem.status = 1
				
				this.shopList.currentIndex = index
				
			}
		}
	}
</script>

<style scoped lang="scss">
	.shop-list {
		.shop-title {
			display: flex;

			.shop-item {
				flex: 1;
				display: flex;
				justify-content: center;
				align-items: center;
				height: 80rpx;


				.shop-icon {
					position: relative;
					margin-left: 10rpx;

					.iconfont {
						position: absolute;
						left: 0;
						width: 16rpx;
						height: 8rpx;
						font-size: 10rpx;
					}

					.up {
						top: -15rpx;
					}

					.down {
						top: -1rpx;
					}
				}


			}

		}
	}
</style>
